l,wght@0,100..900;1,100..900&display=swap');\n\n@import url('https://cdn.jsdelivr.net/npm/modern-normalize@3.0.1/modern-normalize.min.css');\n\n/* maple-mono-latin-400-normal */\n@font-face {\n  font-family: 'Maple Mono';\n  font-style: normal;\n  font-display: swap;\n  font-weight: 400;\n  src: url(https://cdn.jsdelivr.net/fontsource/fonts/maple-mono@latest/latin-400-normal.woff2) format('woff2'), url(https://cdn.jsdelivr.net/fontsource/fonts/maple-mono@latest/latin-400-normal.woff) format('woff');\n}\n\n:root {\n  --background: rgb(8, 8, 6);\n  --secondary: rgb(75, 103, 84);\n  --primary: rgb(250, 249, 238);\n  --accent: rgb(120, 157, 149);\n  --dashboard: hsl(139, 16%, 35%, 15%);\n  --project-item: rgb(168, 214, 204);\n  --outer-box: hsl(55, 55%, 96%, 5%);\n  --notes: rgba(250, 249, 238, 0.2);\n  --not-todos: rgb(20, 10, 10);\n  --text: rgb(238, 237, 231);\n  --button-dark: hsl(139, 16%, 10%);\n  --button-light: hsl(139, 16%, 80%);\n  --item-capsule-red: #FFEFEA;\n  --item-capsule-gray: #ECECEC;\n  --item-capsule-blue: #d8f8ff;\n  --item-capsule-dark: #1E201E;\n  --not-todo-capsule-dark: rgb(44, 25, 25);\n  --form-red: #9d8189a9;\n\n  line-height: 1.5;\n  font-family: \"Inter\", \"Outfit\", \"Work Sans\", \"Roboto Mono\", sans-serif, system-ui;\n}\n\nh1, h2, h3, h4, h5, figure, p, ol, ul {\n  margin: 0;\n}\n\nol[role=\"list\"], ul[role=\"list\"] {\n  list-style: none;\n  padding-inline: 0;\n}\n\nh1, h2, h3, h4, h5 {\n  font-size: inherit;\n  font-weight: inherit;\n}\n\nimg {\n  display: block;\n  max-inline-size: 100%;\n}\n\n/* ---THESE ARE MY OWN PREFERENCES.--- */\np {\n  max-inline-size: 66ch;\n}\n\n/* Removes the arrows inside input type number */\ninput::-webkit-outer-spin-button,\ninput::-webkit-inner-spin-button {\n  -webkit-appearance: none;\n  margin: 0;\n}\n\nmenu,\nol,\nul {\n  padding: 0;\n}\n\nbody {\n  min-height: 100vh;\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n  background-color: var(--background);\n  color: var(--text);\n}\n\nbutton {\n  border: none;\n  border-radius: 8px;\n  padding: 8px 16px;\n}\n\nbutton:hover {\n  cursor: pointer;\n}\n\nbutton.dark {\n  color: var(--text);\n  background-color: var(--button-dark);\n}\n\nbutton.light {\n  color: var(--background);\n  background-color: var(--button-light);\n}\n\nimg.utility {\n  height: 24px;\n  width: 24px;\n}\n\nheader {\n  border-radius: 15px;\n  padding: 16px;\n  margin: 16px 16px 0 16px;\n  background-color: var(--outer-box);\n}\n\np.todo-or-not-todo {\n  font-family: \"Maple Mono\";\n  font-size: 1.5rem;\n  color: orangered;\n}\n\np.todo-or-not-todo span.todo {\n  color: #06923E;\n}\n\np.todo-or-not-todo span.not-todo {\n  color: #b92326;\n}\n\nmain {\n  position: relative;\n  flex: 1;\n  display: grid;\n  grid-template-columns: 1fr 4fr;\n  gap: 16px;\n  margin: 0 16px;\n}\n\ndiv.dashboard, div.content {\n  border-radius: 15px;\n  padding: 16px;\n  color: var(--text);\n  background-color: var(--dashboard);\n}\n\ndiv.dashboard {\n  position: sticky;\n  top: 16px;\n  min-height: 88vh;\n  max-height: min-content;\n  display: flex;\n  flex-direction: column;\n  gap: 24px;\n}\n\np.dashboard-title {\n  font-size: 2rem;\n}\n\ndiv.projects {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n}\n\ndiv.projects-top-all {\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n}\n\ndiv.projects-top {\n  display: flex;\n  justify-content: space-between;\n}\n\np.your-projects {\n  font-size: 1.5rem;\n}\n\nform.add-project div.form-bottom div.form-item,\nform.edit-project div.form-bottom div.form-item {\n  flex-direction: column;\n  align-items: flex-start;\n}\n\nform.edit-project {\n  font-size: 1rem;\n}\n\ndiv.form-tag-list {\n  display: flex;\n  gap: 8px;\n  flex-wrap: wrap;\n  color: var(--background);\n}\n\ndiv.form-tag-option {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n}\n\n/* From Uiverse.io by Galahhad */ \n/* checkbox settings  */\n\n.ui-checkbox {\n  --primary-color: #0C0C0C;\n  --secondary-color: #fff;\n  --